@import '../../styles/_utils//mixins';
@import '../../styles/_utils/_variables';
@import '../../styles/_utils//extends';

.header {
  position: sticky;
  z-index: 50;
  top: 0;

  background-color: $white-primary-clr;

  display: flex;
  justify-content: space-between;
  box-shadow: 0px 1px 0px #E2E6E9;
  font-weight: 700;
  box-sizing: border-box;
}

.header__logo_nav_block {
  padding-top: 13px;
  padding-bottom: 13px;
  display: flex;
  gap: 32px;
  align-items: center;

  @include onTablet {
    padding-top: 0;
    padding-bottom: 0;
  }

  @include onDesktop {
    gap: 48px;
  }
}

.header__logo {
  padding-left: 16px;
}

.header__menu {
  display: none;

  @include onTablet {
    display: flex;
  }
}

.header__menu_list {
    display: flex;
    gap: 32px;

    @include onDesktop {
      gap: 64px;
    }
  }

.header__menu_item {
  display: flex;
}

.header__menu_link {
  position: relative;
  padding-top: 19px;
  padding-bottom: 19px;

  color: $grey-secondary-clr;

  font-weight: 800;
  font-size: 12px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  transition-property: color;

  transition-duration: 0.3s;
}

.header__menu_link:hover {
  color: $grey-primary-clr;

  &::after {
    left: 0;
    right: 0
  }
}

.header__menu_link::after {
  display: block;
  content: "";
  height: 4px;
  background-color: $grey-primary-clr;
  border-radius: 8px;
  position: absolute;
  top: 52px;
  left: 50%;
  right: 50%;

  transition-property: color, left, right;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.header_icons_container {
  display: flex;
  flex-direction: row;
}

.header__cart_icon {
  display: none;

  @include onTablet {
    display: grid;
    place-content: center;
    border-left: 1px solid #E2E6E9;
  }
}

.header_fav_counter {
  visibility: hidden;
  @extend %header_fav_counter;

  &_active {
    visibility: visible;

    @extend %header_fav_counter
  }
}

.header_cart_counter {
  @extend %header_fav_counter;
  visibility: hidden;
  right: 10px;

  &_active {
    visibility: visible;
    right: 10px;

    @extend %header_fav_counter;
  }
}

.header_icon_counter {
  visibility: hidden;
  position: absolute;
  background-color: $icon-counter-clr;

  border-radius: 50%;
  width: 14px;
  height: 14px;
  text-align: center;

  color: #FFFFFF;
  font-size: 9px;
  font-weight: 600;
  padding: 2px;

  right: 62px;
  top: 12px;

  z-index: 1;

  &_active {
    visibility: visible;

    @extend %header_fav_counter;

    @include onTablet {
      display: none;
    }
  }
}

.header__cart_icon_link {
  @include onTablet {
    position: relative;

    padding-left: 17px;
    padding-right: 17px;
    padding-bottom: 12px;
    padding-top: 18px;
    transition: 0.3s;
  }
}

.header__cart_icon_link:hover {
    &::after {
    left: 0;
    right: 0
  }
}

.header__cart_icon_link::after {
  display: block;
  content: "";
  height: 4px;
  background-color: $grey-primary-clr;
  border-radius: 8px;
  position: absolute;
  top: 52px;
  left: 50%;
  right: 50%;

  transition-property: color, left, right;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.header__burger_menu {
  display: flex;
  align-items: center;
  border-left: 1px solid #E2E6E9;
  cursor: pointer;

  @include onTablet {
    display: none;
  }
}

.header__burger_menu_link {
  padding: 17px;
}

.header__menu_link_is_active {
  color: $grey-primary-clr !important;

  &::after {
    display: block;
    content: "";
    height: 4px;
    background-color: $grey-primary-clr;
    border-radius: 8px;
    position: absolute;
    top: 52px;
    left: 0;
    right: 0;
  }
}

.burger {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #FFFFFF;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  transform: translateX(100%);
  transition: transform 0.5s;
}

.burger:target {
  transform: translateX(100%);
}

.burger_open {
  transform: translateX(0);
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  background-color: #FFFFFF;
  transition: transform 0.5s;
}

.burger_open:target {
  transform: translateX(0);
}

.burger_open_header {
  display: flex;
  justify-content: space-between;
  box-shadow: 0px 1px 0px #E2E6E9;
}

.burger_header__logo {
  padding-left: 16px;
  padding-top: 13px;
  padding-bottom: 13px;
}

.burger_header__icon {
  color: $grey-primary-clr;
  padding: 17px;
}

.burger__menu_list {
  padding-top: 13px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 13px;
}

.burger__menu_link {
  position: relative;
  padding-top: 19px;
  padding-bottom: 19px;

  color: $grey-secondary-clr;

  font-weight: 800;
  font-size: 12px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  transition-property: color;

  transition-duration: 0.3s;
}


.burger__menu_footer {
  display: flex;
  height: 60px;
  justify-content: space-around;
  box-shadow: inset 0px 1px 0px #e2e6e9;
}


.burger__menu_footer_pic {
  display: flex;
  flex-basis: 50%;
  justify-content: center;

  border-right: 1px solid #e2e6e9;
}

.burger__cart_icon_link {
  position: relative;
  padding: 25px 90px;

  transition-property: color;

  transition-duration: 0.3s;
}

.burger__cart_icon_link:hover {
  color: $grey-primary-clr;

  &::after {
    left: 0;
    right: 0
  }
}

.burger__cart_icon_link::after {
  display: block;
  content: "";
  height: 4px;
  background-color: $grey-primary-clr;
  border-radius: 8px;
  position: absolute;
  bottom: 1px;
  left: 50%;
  right: 50%;

  transition-property: color, left, right;
  transition-duration: 0.3s;
  transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.burger__cart_icon_link_is_active {
  color: $grey-primary-clr !important;
  position: relative;
  padding: 25px 90px;

  transition-property: color;

  transition-duration: 0.3s;

  &::after {
    display: block;
    content: "";
    height: 4px;
    background-color: $grey-primary-clr;
    border-radius: 8px;
    position: absolute;
    bottom: 1px;
    left: 0;
    right: 0;
  }
}

.burger_fav_counter {
  visibility: hidden;
  @extend %burger_fav_counter;

  &_active {
    visibility: visible;

    @extend %burger_fav_counter;
  }
}

