body,
p,
ul,
li,
section,
nav,
a,
main,
h1,
h2,
h3,
h4,
button {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
}

button {
  cursor: pointer;
}

* {
  box-sizing: border-box;
  font-family: Mont, sans-serif;
}

html, body, #root {
  min-height: 100vh;
}

body {
  overflow-x: hidden;
}
